const galleryRef = document.querySelector('.gallery');
const listItems = images.reduce(
  (html, item) =>
    html +
    `<li class="gallery-item">
          <a class="gallery-link" href="${item.original}">
            <img
              class="gallery-image"
              src="${item.preview}"
              alt="${item.description}"
            />
          </a>
        </li>`,
  ''
);

galleryRef.innerHTML = listItems;

Object.assign(galleryRef.style, {
  display: 'flex',
  flexWrap: 'wrap',
  columnGap: '24px',
  rowGap: '48px',
  listStyleType: 'none',
  padding: '100px',
});
const items = galleryRef.querySelectorAll('.gallery-item');
items.forEach(item => (item.style.width = 'calc((100% - 48px) / 3)'));

var lightbox = new SimpleLightbox('.gallery a', {
  captionsData: 'alt',
  captionDelay: 250,
});
